import React from "react";
import Grid from "@material-ui/core/Grid";
import PropTypes from "prop-types";
import { Typography } from "@material-ui/core";

class VitaLine extends React.Component {
  render() {
    return (
      <React.Fragment>
        <Grid item xs={3} className="highlight-color">
          <Typography color="primary">{this.props.year}</Typography>
        </Grid>
        <Grid item xs={9}>
          <Typography color="primary">{this.props.content}</Typography>
        </Grid>
      </React.Fragment>
    );
  }
}

VitaLine.propTypes = {
  year: PropTypes.string.isRequired,
  content: PropTypes.string.isRequired,
};

export default VitaLine;
