import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import VitaLine from "./vita/VitaLine";
import CategoryHeader from "./common/CategoryHeader";

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(1),
    textAlign: "center",
    color: theme.palette.text.secondary,
  },
}));

const vitaEntries = [
  {
    id: "vita-1",
    year: "1974 - 1979",
    content: "Studium der Biologie und Chemie, Universität zu Köln.",
  },
  {
    id: "vita-2",
    year: "1998 - 2009",
    content:
      "Studiendirektorin für die Fächer Biologie und Chemie. Fachleitung Biologie und Kernseminarleitung an Zentren für schulpraktische Lehrerausbildung im Regierungsbezirk Köln.",
  },
  {
    id: "vita-3",
    year: "2007 - 2009",
    content:
      "Mitglied der Lehrplankommission Biologie für die gymnasiale Oberstufe, Ministerium für Schule und Bildung des Landes Nordrhein-Westfalen.",
  },
  {
    id: "vita-4",
    year: "2009 - 2011",
    content:
      "Entwicklung des Bachelor- und Masterstudiengangs des Fachdidaktik-Studiums Biologie für das Lehramt an Gymnasien, Gesamtschulen sowie an Berufskollegs, Universität zu Köln, Department für Biologie.",
  },
  {
    id: "vita-5",
    year: "ab 2009",
    content:
      "Entwicklung des Bachelor- und Masterstudiengangs des Fachdidaktik-Studiums Biologie für das Lehramt an Gymnasien, Gesamtschulen sowie an Berufskollegs, Universität zu Köln, Department für Biologie und Leitung der Fachdidaktik Biologie, Universität zu Köln, Department für Biologie.",
  },
  {
    id: "vita-6",
    year: "",
    content:
      "Mitglied der Akkreditierungsagentur ASIIN für Studiengänge der Ingenieurwissenschaften, der Informatik, der Naturwissenschaften und der Mathematik.",
  },
];

export default function NestedGrid() {
  const classes = useStyles();

  var vitaLinesJsx = vitaEntries.map((e) => (
    <Grid key={e.id} container item xs={12} spacing={3}>
      <VitaLine year={e.year} content={e.content} />
    </Grid>
  ));

  return (
    <div className={classes.root}>
      <CategoryHeader category="Kurz-Vita" />
      <Grid container spacing={1}>
        {vitaLinesJsx}
      </Grid>
    </div>
  );
}
